nav{
    display:flex;
    justify-content:center;
    align-items: center;
    background-color:black;
    color:white;
    font-size: 1.6em;
    gap:1em;
    height:2.5em;
    overflow-y: hidden;
}
.logo img{
    width: 100%;
    height:2em;
}
.searchbar{
    font-size: 12px;
    display:flex;
    gap:.5em;
    align-items:center;
    width: 70em;
}
.location{
}
.loci{
    position: relative;
    top:-6px;
    font-size: 1.2em;
}
.searchselect{
    width:3.5em;
    height:2.5em;
    background-color: #a7a3a3;
    border-radius: 8px 0px 0px 8px;
   
}
#searchbox{
    height:2.5em;
    width: 91.67%;
    outline: none;
    position: relative;
    top:-1px;
    width:88.5%
}
.searchbox{
    border: 3px solid black;
    border-radius: 10px 0px 0px 10px;
    width:80%;
}
.searchbox:hover{
    border:3px solid orange
}
.searchi{
    height: 1.5em;
    color:white;
}
.searchii{
    background-color:orange;
    padding:.9em .5em .5em .5em;
    position: relative;
    top:2px;
    border-radius: 0px 9px 9px 0px;
}
.rightcontainer{
    font-size:12px;
    display:flex;
    gap:1.5em;
    align-items: center;
}
.userbold, .orderbold{
    font-weight: bolder;
    position: relative;
    top:-4px;
    font-size: 1.1em;
}
.carti{
    height:3em;
    border-radius: 5px 5px 5px 15px;
}
.cartno{
    position:relative;
    top:-1.8em;
    font-size: large;
    padding:3px;
}
.accountuser{
    display:block;
}
