.image img{
    height:31px;
    width:103px;
}
.image{
    display:flex;
    justify-content: center;
    align-items: center;
}
.left-hr{
   min-width: 28%;
    float: left;
}
.right-hr{
   min-width:28%;
    float: right;
}
.content{
    display: flex;
    justify-content: center;
}
.terms-condition{
   max-width:22em;
   font-size: .8em !important;
   .condition{
    color:blue;
   }
   .condition:hover{
    color:red;
    text-decoration: underline;
   }
}
.Sign-name{
    font-weight:400;
    font-size: 1.5em;
    float:left;
    position: relative;
    top:1em;
}
.email-name{
 font-weight:450;
 float:left;
}
#email{
min-width:17em;
min-height: 1.5em;
}
.continue{
    min-width:17.5em;
    min-height: 1.8em;
    border:none;
    background-color:rgb(252, 184, 56);
    border-radius: 5px;
}
.signin{
    padding:0em 1.5em 1em 1.5em;
    border:1px solid rgb(224, 215, 215);
    border-radius: 5px;
    display:flex;
    flex-direction: column;
}
.hr{
    margin-top:1.5em;
}
.accnt{
    display:flex;
    justify-content: center;
    font-size: .7em;
    margin:1em;
}
.accnt-p{
 border:1px solid rgb(154, 147, 147);
 padding:.4em 5em;
 border-radius:5px;
 box-shadow: 0em .06em .1em 0em rgb(173, 198, 173);
}
.hr-down{
    width: 60%;
  
}
.copywright{
    display:flex;
    justify-content: center;
    gap:1em;
    font-size: small;
}
.p-copyw:hover{
    color:red;
    text-decoration: underline;
}
.p-copyw{
    color:blue;
}
#country{
padding: .1em .5em;
border-radius: 5px;
border-color: rgb(174, 171, 166);
box-shadow: 0em 0em .2em;
}
.number-mo{
    display: flex;
    gap:1em;
    margin-top: .5em;
}
.input-signup{
    border-radius: 5px;
    border:1px solid rgb(143, 139, 139);
    height:1.7em;
    
}
.input-name{margin-top:.5em}
.Mobileno{
margin-top: .6em;
font-weight: 600;
}
.Signup-name{
    font-weight: 600;
    margin-top: .6em;
}
.input-signup:hover{
    box-shadow: 0em 0em .1em .1em rgb(125, 200, 238);
}
.hr-singup{
    margin-top:2em;
    width: 100%;
}