.producti{
    height: 10em;
    max-width: 15em;
}
.productscover p{
    font-size:.8em !important ;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.product{
    display:flex;
    flex-wrap: wrap;
    gap:1firstem;
}
.price{
    color:rgb(231, 35, 35);
    font-weight: 400;
}
.productscover{
    background-color: white;
    padding:1em 1em 0em 1em;
    margin:1em 0em;
    border-radius: 15px;
}
.desc{
    max-width:15em;
}
.rating{
   
}
.flex{
    display:flex;
    justify-content: space-between;
    align-items: center;
}
iframe{
    width: 100%;
    height:50em;
}
.original-price{
    text-decoration: line-through rgb(79, 64, 64) ;
    color:black;
}